import React, { useState } from 'react';
import './App.css';

import Cards from './components/Cards.jsx';
import SearchBar from './components/SearchBar.jsx';
import data from './data.js';

function App() {

  
  const [inputValue, setInputValue] = useState('')
  const [dataArray, setDataArray] = useState(data)

  const searchCity = () => {

    const filteredData = data.filter(item => item.name.toLowerCase() === inputValue.toLowerCase())
    
    setDataArray(filteredData)
  }

  const deleteCity = (item) => {

    const deletedCity = dataArray.filter(city => city.id !== item.id)
    setDataArray(deletedCity);
  }

  console.log(dataArray);

  return (
    <div className="App">
      <div>
        <SearchBar
          onSearch={(ciudad) => alert(ciudad)}
          inputValue = { inputValue }
          setInputValue = { setInputValue }
          searchCity = { searchCity }
        />
      </div>
      <hr />
      <div>
        <Cards
          cities={ dataArray }
          deleteCity = { deleteCity }
        />
      </div>
      <hr />
    </div>
  );
}

export default App;
